$black:      #15141d; //#000000 default
$blue:       #0336FF; //#244cde default
$indigo:     #6610f2; //no match
$purple:     #8a42f5;
$pink:       #ff0266; //#ff3465 default (F9 Magenta)
$red:        #dc3545; //no match
$orange:     #fd7e14; //no match
$yellow:     #ffae03; //#ffba01 default
$green:      #00c268;
$teal:       #20c997; //no match
$cyan:       #17a2b8; //no match
$light-white:#f7f7f7;
$light-grey: #d8d8d8;

$pearl-white:#ececec;
$light-black: lighten($black, 60%);

$body-color:    $black;
$body-bg:       $light-white;

$link-color:        $blue;
$link-hover-color:  $black;
$link-hover-decoration: none;
$link-decoration: none;
$font-family-base: 'Silka', sans-serif;
$line-height-base: 1.625;
$padding-width: 15px;

$enable-shadows: false;

$container-width: 1334px; //Standart is 1140px.

$container-max-widths: (
  xl: $container-width
);

$breadcrumb-padding-y:      0.625rem;
$breadcrumb-padding-x:      0;
$breadcrumb-item-padding:   1rem;
$breadcrumb-margin-bottom:  1rem;

$breadcrumb-bg: none;
$breadcrumb-divider-color: $light-black;
$breadcrumb-active-color: $light-black;
$breadcrumb-divider: quote(">");

$btn-border-width:            2px;

$border-radius:               0;
$border-radius-lg:            0;
$border-radius-sm:            0;

$btn-border-radius:           1.5rem;
$btn-border-radius-lg:        1.75rem;
$btn-border-radius-sm:        1.25rem;

$input-btn-padding-y:         .5rem;
$input-btn-padding-y-sm:      .25rem;
$input-btn-padding-y-lg:      .75rem;

$btn-padding-x:               2rem;
$btn-padding-x-sm:            1rem;
$btn-padding-x-lg:            3rem;